.home {
  margin-top: 10vh;
  width: 100%;
  grid-column: 2 / span 10;
  display: grid;
  .favorites-container {
    place-self: center;
		h2 {
			font-size: 25pt
		}
  }
  .dashboard-title {
    height: 1em;
    text-align: center;
    font-size: xx-large;
  }
  .burger-of-the-day {
    place-self: center;
    border-radius: 10px;
    display: inline-flex;
    flex-direction: column;
    h2 {
      font-weight: lighter;
      font-size: 20pt;
    }
    .price {
      font-size: 25pt;
      font-weight: normal;
    }
    .burger-name {
      font-size: 25pt;
      font-weight: bold;
    }
    .burger-stats {
      text-align: right;
      margin-bottom: 4px;
      margin-right: 4px;
    }

    box-shadow: 3px 3px 10px;
  }
}
